import React , {Component} from 'react';
import {Grid, Cell} from 'react-mdl';
import {img} from 'react-mdl';
import pozacontact from '../Images/pozacontact.jpeg';

class Landingpage extends Component{
	render(){
		return(
			<div style={{width: '100%', margin:'auto'}}>
				<Grid className="landing-grid">
					<Cell col={12}>
						< img
						src = {pozacontact}
						alt="avatar"
						className="avatar-img"
						/>
					
					<div className="banner-text">
						<h1>Fullstack developer</h1>
					 <hr></hr>
					 <p> C# | Coded UI Test | Ranorex | JavaScript | NPM | React | </p>
					 
					 <div className="social-Links">
					 
					 {/*LinkeId */}
					 <a href="http://google.com" rel="noopener noreferrer" target="_blank">
					 <i className="fa fa-Linkedin-square" rel="noopener noreferrer" aria-hidden="false"/>
					  </a>
					  
					  {/*Github */}
					  <a href="https://github.com/opreararesstefan" rel="noopener noreferrer" target="_blank">
					  <i className="fa fa-github-square" aria-hidden="false"/>
					  </a>
					  
					  {/*Freecodecamp */}
					  <a href="http://freecodecamp.com" rel="noopener noreferrer" target="_blank">
					   <i className="fa fa-free-code-camp" aria-hidden="false"/>
					   </a>
					   
					  {/*Youtube */}
					  <a href="http://youtube.com" rel="noopener noreferrer" target="_blank">
					   <i className="fa fa-youtube-square" aria-hidden="false"/>
					   </a>
					   
					 </div>
					</div>
					</Cell>
				</Grid>
			</div>

		)
	}
}

export default Landingpage;