import React , {Component} from 'react';
import {Grid,Cell} from 'react-mdl';
import{props} from 'react';



class Berufserfahrung extends Component{
	
	constructor(){
			   super(props)
			   this.state={
				hidden:false
				   
			   };
			   this.toggleHidden= this.toggleHidden.bind(this)
		   }
		   
		 toggleHidden(){
			 const {hidden}= this.state;
			   this.setState({
			   hidden: !hidden  })
    }
	
			JobDescription(){
				return(
				
				<ul>
			<li>{this.props.jobDescription}</li>
			</ul>
	
	)
	 }
	
	render(){
		return(
			<Grid>
				<Cell col={2}>
				<p>{this.props.startYear}-{this.props.endYear}</p>
				</Cell>
				
				
				
				<Cell col={8}>
					<h4 style={{marginTop:'0px'}}>{this.props.JobName}</h4>
					<h6 >{this.props.firmeName}</h6>
					<h6 >{this.props.adresse}</h6>	
				
					<Cell col={8}>  <button onClick={()=>this.toggleHidden()} > Details</button></Cell>
						
						<div col={8} className="detail-coll">{this.state.hidden&& this.JobDescription()}</div>
					
							
				</Cell>
			</Grid>
			
		)
	}
}


export default Berufserfahrung;