import React  from 'react';
import {Tabs, Tab,  Grid, Cell,Card, CardTitle,CardText, CardActions, Button, CardMenu, IconButton} from "react-mdl"; 

class Projects extends React.Component {
    constructor(props) {
        super(props)
        this.state = { activeTab: 0 };
    }
		toggleCategories(){
		
		 if(this.state.activeTab===0){
	return (
	<div className="project-grid">
	<Grid>
	
	<Card shadow={5} style={{minWidth :'450', margin:'12px'}}>
		<CardTitle style={{color:'#fff', height:'176px', background:'url(https://miro.medium.com/max/1200/1*y6C4nSvy2Woe0m7bWEn4BA.png) center/cover'}}>
		</CardTitle>
		<CardText>
			Snake Game
		</CardText>
		<CardActions>
			<Button colored><a href="https://opreararesstefan.github.io" target="_blank" rel="noopener noreferrer">GitHub</a></Button>
			<Button colored>CodePen</Button>
			<Button colored><a href=" https://anca-oprea.github.io/SnakeGame/" target="_blank" rel="noopener noreferrer"> LiveDemo</a></Button>
		</CardActions>
			<CardMenu style={{color:'#fff'}}>
			<IconButton name="share" />
			</CardMenu>
	</Card>
	
	
	<Card shadow={5} style={{minWidth :'450', margin:'12px'}}>
		<CardTitle style={{color:'#fff', height:'176px', background:'url(https://miro.medium.com/max/1200/1*y6C4nSvy2Woe0m7bWEn4BA.png) center/cover'}}>
		</CardTitle>
		<CardText>
		Tic Tac Toe
		</CardText>
		<CardActions>
			<Button colored>GitHub</Button>
			<Button colored>CodePen</Button>
			<Button colored>LiveDemo</Button>
		
		</CardActions>
			<CardMenu style={{color:'#fff'}}>
			<IconButton name="share" />
			</CardMenu>
	</Card>
	
	<Card shadow={5} style={{minWidth :'450', margin:'12px'}}>
		<CardTitle style={{color:'#fff', height:'176px', background:'url(https://miro.medium.com/max/1200/1*y6C4nSvy2Woe0m7bWEn4BA.png) center/cover'}}>
		</CardTitle>
		<CardText>
		Während sich die aktuelle Site im Aufbau befindet, verwenden Sie bitte die alte Site<br/>
		<a href = "https://opreararesstefan.github.io" target = "_blank" rel="noopener noreferrer">https://opreararesstefan.github.io</a>
		</CardText>
		<CardActions>
			<Button colored>GitHub</Button>
			<Button colored>CodePen</Button>
			<Button colored>LiveDemo</Button>
		
		</CardActions>
			<CardMenu style={{color:'#fff'}}>
			<IconButton name="share" />
			</CardMenu>
	</Card>
	
	</Grid>
	</div>
	)
	}
	}
	
    render() {
        return (
            <div className="category-tabs">
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
                    <Tab>Portfolio</Tab>
                </Tabs>
				    <section ClassName="projects-grid">
						<Grid className="projects-grid">
						<Cell col={12}>
						<div className="content">{this.toggleCategories()}</div>
						</Cell>
						
						</Grid>
				
					</section>
					
            </div>    
        )
    }
}
export default Projects;