import React , {Component} from 'react';
import {Grid,Cell} from 'react-mdl';
import{props} from 'react';


class Ausbildung extends Component{
	
	constructor(){
			   super(props)
			   this.state={
				hidden:false
			   };
			   this.toggleHidden= this.toggleHidden.bind(this)
			}
		   
		 toggleHidden(){
			 const {hidden}= this.state;
			   this.setState({
			   hidden: !hidden  })
			}
	
			schoolDescription(){
			return(
			<ul>
			<li>{this.props.schoolDescription}</li>
			</ul>
				)
			}
	
	render(){
		return(
			<Grid>
				<Cell col={2}>
				<p>{this.props.startYear}-{this.props.endYear}</p>
				</Cell>
				
				<Cell col={8}>
				<h4 style={{marginTop:'0px'}}>{this.props.schoolName}</h4>
				<h6 style={{marginTop:'0px'}}>{this.props.abschluss}</h6>
				<h6 style={{marginTop:'0px'}}>{this.props.adresse}</h6>
				<Cell col={8}>  <button onClick={()=>this.toggleHidden()} > Details</button></Cell>
				<div col={8} className="detail-coll" >{this.state.hidden&& this.schoolDescription()}</div>
               </Cell>
			</Grid>
		)
	}
}

export default Ausbildung;