import React , {Component} from 'react';
import {Grid,Cell} from 'react-mdl';
import {List, ListItem, ListItemContent} from 'react-mdl';
import pozacontact from '../Images/pozacontact.jpeg';

class Contact extends Component{
	render(){
		return(
		    <div className="contact-body">
				<Grid  className="contact-grid">
					<Cell col={6}> 
					<h2>Oprea Rares Stefan</h2>
					<img 
					src={pozacontact}
					alt="avatar"
					style={{height :'250px'}}
					/>
					<p style={{ width: '75%', margin:'auto', paddingTop: '1em'}}>Als erfahrener Programmierer mit Bachelor-Abschluss im Bereich Digital Automation 
					und internationaler Erfahrung suche ich neue Herausforderungen im Bereich „Digital Intelligence“ sowie die Möglichkeit zur beruflichen
					Weiterentwicklung auf Führungsebene.
					Ich beherrsche alle gängigen Programmiersprachen auf verschiedenen Systemen und bin es gewohnt, 
					eigenverantwortlich nach Lösungen zu suchen und auch größere Projekte zu leiten. </p>
					</Cell>
					<Cell col={6}> 
					<h2>Contact</h2>
					<hr border={{borderTop: '3px solid #e22947'}} />
					<div className="contact-list">
					<List>
						<ListItem>
							<ListItemContent style={{fontSize: '30px', fontFamily:'Anton'}} >
							Email: opreararesstefan@gmail.com
							</ListItemContent>
						</ListItem>
							<ListItem>
							<ListItemContent style={{fontSize: '30px', fontFamily:'Anton'}} >
							Geboren am: 09.06.1985, Barlad, Rumänien
							</ListItemContent>
						</ListItem>
							<ListItem>
							<ListItemContent style={{fontSize: '30px', fontFamily:'Anton'}} >
							Handy: (+49) 151 7127 9327
							</ListItemContent>
						</ListItem>
						<ListItem>
							<ListItemContent style={{fontSize: '30px', fontFamily:'Anton'}} >
							Familienstand :1 Kind, verheiratet
							</ListItemContent>
						</ListItem>
					</List>
					</div>
					</Cell>
				</Grid>
			
			</div>
			)
	}
}

export default Contact;